<template>
  <div id="app">
    <div class="app-container">
      <!-- autocomplete -->
      <app-autocomplete class="elevated" :world-countries="storeUserOptions" @select="onSelect"></app-autocomplete>
      <!-- results -->
      <template v-if="storeUserData.length > 0">
        <div v-for="(stats, index) of storeUserData" :key="index">
          <app-statistics class="elevated" :statistics="stats" @notify="onNotify"></app-statistics>
        </div>
      </template>
      <!-- no data -->
      <template v-else>
        <div class="no-data">
          <div>No Countries Selected</div>
          <div>Add Some</div>
        </div>
      </template>
      <app-toast :duration="1500" :message="flashMessage"></app-toast>
      <!-- <div>{{storeUserHistory.length}}</div>
      <div>{{storeUserData}}</div>-->
    </div>
  </div>
</template>

<script>
import AppAutocomplete from "./components/AppAutocomplete";
import AppStatistics from "./components/AppStatistics";
import AppToast from "./components/AppToast";
import { mapGetters } from "vuex";

export default {
  name: "App",

  components: {
    AppAutocomplete,
    AppStatistics,
    AppToast
  },

  data() {
    return {
      flashMessage: ""
    };
  },

  computed: {
    ...mapGetters([
      "storeBookmarks",
      "storeUserData",
      "storeUserHistory",
      "storeUserCountries",
      "storeUserOptions"
    ])
  },

  methods: {
    onSelect(country) {
      if (!this.storeUserCountries.includes(country.toLowerCase())) {
        this.$store.dispatch("addData", country.trim().toLowerCase());
      }
    },

    onNotify(message) {
      this.flashMessage = message;
    }
  },

  created() {
    this.$store.dispatch("initialize");
  }
};
</script>

