import axios from "axios";

const QUERIES = {
  options: () => {
    return `
    query optionsQuery {
      countries {
        country
      }
    }`;
  },
  stats: countries => {
    return `
    query statsQuery {
        stats(countries: ${JSON.stringify(countries)}) {
          country
          cases
          deaths
          active
          critical
          recovered
          todayCases
          todayDeaths
          casesPerOneMillion
        }
      }
    `;
  },
  history: countries => {
    return `
    query historyQuery {
        history(countries: ${JSON.stringify(countries)}) {
          country
          timeline {
            cases { 
              date
              total
              today
              growth
            }
            deaths { 
              date
              total
              today
              growth
            }
            recovered { 
              date
              total
              today
              growth
            }
          }
        }
      }    
    `;
  }
};

// FIXME
// refactor the axios requests in one function
class Service {
  static getOptions() {
    return new Promise((resolve, reject) => {
      const query = QUERIES.options();
      axios
        .post("/graphql", { query: query })
        .then(result => {
          const { data } = result.data;
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  static getData(countries = []) {
    return new Promise((resolve, reject) => {
      const query = QUERIES.stats(countries);
      axios
        .post("/graphql", { query: query })
        .then(result => {
          const { data } = result.data;
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  static getHistory(countries = []) {
    return new Promise((resolve, reject) => {
      const query = QUERIES.history(countries);
      axios
        .post("/graphql", { query: query })
        .then(result => {
          const { data } = result.data;
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
}

export default Service;
