import Vue from "vue";
import Vuex from "vuex";
import types from "./mutations";
import Storage from "../Storage";
import Service from "../Service";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    bookmarks: [],
    userData: [],
    userOptions: [],
    userHistory: []
  },

  getters: {
    storeBookmarks: state => {
      return state.bookmarks;
    },

    storeUserData: state => {
      return state.userHistory.map(item => {
        const { country, timeline: { cases, deaths, recovered } = {} } = item;
        return {
          country,
          cases: [...cases].pop(),
          deaths: [...deaths].pop(),
          recovered: [...recovered].pop()
        };
      });
      // return state.userData;
    },
    storeUserOptions: state => {
      return state.userOptions;
    },

    storeUserCountries: state => {
      return state.userData.map(item => item.country.toLowerCase());
    },

    storeUserHistory: state => {
      return state.userHistory;
    }
  },

  mutations: {
    [types.BOOKMARKS_LOAD](state, payload) {
      Vue.set(state, "bookmarks", payload.bookmarks);
    },
    [types.USERDATA_LOAD](state, payload) {
      Vue.set(state, "userData", payload.data);
    },
    [types.USEROPTIONS_LOAD](state, payload) {
      Vue.set(state, "userOptions", payload.options);
    },
    [types.USERHISTORY_LOAD](state, payload) {
      Vue.set(state, "userHistory", payload.history);
    }
  },

  actions: {
    loadOptions: async ({ commit }) => {
      try {
        const options = await Service.getOptions();
        commit({
          type: "USEROPTIONS_LOAD",
          options: options.countries.map(item => item.country.toLowerCase())
        });
      } catch (error) {
        // TODO
        // set a timeout in the server
        // commit error
        console.log(error);
      }
    },

    loadData: async ({ commit }, countries = []) => {
      try {
        // // history
        const { history } = await Service.getHistory(countries);
        commit({
          type: "USERHISTORY_LOAD",
          history: history
        });
        // // history
        const { stats } = await Service.getData(countries);
        commit({
          type: "USERDATA_LOAD",
          data: stats
        });
      } catch (error) {
        // set error
        console.log(error);
      }
    },

    addData: async ({ getters, commit }, country = "") => {
      try {
        // // history
        const { history } = await Service.getHistory([country]);
        commit({
          type: "USERHISTORY_LOAD",
          history: [...getters.storeUserHistory, history[0]]
        });
        // // history
        const { stats } = await Service.getData([country]);
        commit({
          type: "USERDATA_LOAD",
          data: [...getters.storeUserData, stats[0]]
        });
      } catch (error) {
        // set error
        console.log(error);
      }
    },

    removeData: ({ getters, commit }, country) => {
      const userData = [...getters.storeUserData].filter(item => {
        return item.country.toLowerCase() !== country.trim().toLowerCase();
      });
      commit({
        type: "USERDATA_LOAD",
        data: userData
      });
    },

    loadBookmarks: async ({ commit }) => {
      try {
        const bookmarks = await Storage.async_getItems();
        commit({
          type: "BOOKMARKS_LOAD",
          bookmarks: bookmarks || []
        });
      } catch (error) {
        // set error
        console.log(error);
      }
    },

    addBookmark: async ({ getters, commit }, country) => {
      if (!getters.storeBookmarks.includes(country)) {
        const bookmarks = [...getters.storeBookmarks, country];
        try {
          await Storage.async_setItems(bookmarks);
          commit({
            type: "BOOKMARKS_LOAD",
            bookmarks: bookmarks
          });
        } catch (error) {
          // set error
          console.log(error);
        }
      }
    },

    removeBookmark: async ({ getters, commit }, country) => {
      if (getters.storeBookmarks.includes(country)) {
        const bookmarks = getters.storeBookmarks.filter(item => item !== country);
        try {
          await Storage.async_setItems(bookmarks);
          commit({
            type: "BOOKMARKS_LOAD",
            bookmarks: bookmarks
          });
        } catch (error) {
          // set error
          console.log(error);
        }
      }
    },

    initialize: async ({ getters, dispatch }) => {
      try {
        await dispatch("loadBookmarks");
        await dispatch("loadOptions");
        await dispatch("loadData", getters.storeBookmarks);
      } catch (error) {
        // set error
        console.log(error);
      }
    }
  },
  modules: {}
});
